import React from 'react'

import ArticleListItem from "../blog/ArticleListItem"


import client from "./utils/client"
import {withRouter} from "react-router-dom"

class Europe extends React.Component {  
    state = {
    articles: [],
   
  }  
  
  componentDidMount() {
   
    client.getEntries({
      "content_type":"blogPost",
        "fields.country": "europe"
    }
    ).then((entries) => {
      entries.items.forEach(entry => {
        this.setState({
          articles: entries.items,
          
        })
      })
    }
    )}

  render() {
  const articles = this.state.articles.map((article, i) =>
      <ArticleListItem key={i} article={article} />
      )
   
    return (
      <div>
        {this.state.isLoading ? <div>
        <p className="loading">–ó–∞–≥—Ä—É–∑–∫–∞...</p> 
        </div> : <div className="article_list_europe">{articles}</div> }
      </div>
    )
  }
}

export default Europe