import React from 'react';
import ReactDOM from 'react-dom'
import { Route, BrowserRouter, Switch, HashRouter} from 'react-router-dom';

import Blog from "./app/blog/Blog"
import BlogItem from "./app/blog/BlogItem"
import Main from "./app/blog/Main"
import Countries from "./app/blog/Countries"
import MexicoComponent from "./app/blog/components/MexicoComponent"
import ArgentinaComponent from "./app/blog/components/ArgentinaComponent"
import EcuadorComponent from "./app/blog/components/EcuadorComponent"
import PeruComponent from "./app/blog/components/PeruComponent"
import ColombiaComponent from "./app/blog/components/ColombiaComponent"
import CostaricaComponent from "./app/blog/components/CostaricaComponent"
import ChileComponent from "./app/blog/components/ChileComponent"
import NorwayComponent from "./app/blog/components/NorwayComponent"
import BrasilComponent from "./app/blog/components/BrasilComponent"
import EuropeComponent from "./app/blog/components/EuropeComponent"
import ItalyComponent from "./app/blog/components/ItalyComponent"
import About_me from "./app/blog/About_me"
import Random from "./app/blog/Random"

ReactDOM.render((
 
  <HashRouter>
        <Route exact path="/" component={Main} />
        <Route path="/countries" component={Countries} />
        <Route exact path="/articles/:slug" component={BlogItem} />
        <Route exact path="/articles" component={Blog} />
        <Route path="/mexico" component={MexicoComponent} />
        <Route path="/about" component={About_me} />
        <Route path="/random" component={Random} />
        <Route exact path="/argentina" component={ArgentinaComponent} />
        <Route path="/ecuador" component={EcuadorComponent} />
        <Route path="/colombia" component={ColombiaComponent} />
        <Route path="/peru" component={PeruComponent} />
        <Route path="/chile" component={ChileComponent} />
        <Route path="/brasil" component={BrasilComponent} />
        <Route path="/norway" component={NorwayComponent} />
        <Route path="/costa-rica" component={CostaricaComponent} />
        <Route path="/europe" component={EuropeComponent} />
        <Route path="/italy" component={ItalyComponent} />
        
  </HashRouter>
), document.getElementById('root'))
