import React from "react"
import { Link, HashRouter } from 'react-router-dom';
import "../sass/main.sass"

import BlogMenu from "./BlogMenu"
import Argentina from "./Argentina"
import Mexico from "./Mexico"
import Ecuador from "./Ecuador"
import Peru from "./Peru"
import Colombia from "./Colombia"
import Costarica from "./Costarica"
import Chile from "./Chile"
import Norway from "./Norway"
import Europe from "./Europe"
import Brasil from "./Brasil"
import Italy from "./Italy"
import Footer from "./Footer"



class BlogContent extends React.Component {

    componentDidUpdate() {
        this.page.focus()
      }

    render() {

        return(
            <div>
                <BlogMenu />
               
                   <div className="note">
                   <div class="logo_text">
                   <h3 tabIndex="-1" ref={(page) => { this.page = page}}>–ö—Å–µ–Ω–∏—è - –ø—É—Ç–µ—à–µ—Å—Ç–≤–µ–Ω–Ω–∏—Ü–∞</h3>
                       <h4>–ò—Å—Ç–æ—Ä–∏–∏ –æ –º–æ–∏—Ö –ø—É—Ç–µ—à–µ—Å—Ç–≤–∏—è—Ö</h4>
                   </div>
                       <div className="note_grid">
                           <div className="note_text">
                               <h3>{this.props.title}</h3>
                               <p dangerouslySetInnerHTML={this.props.content}></p>
                               <p>–î–∞—Ç–∞: {this.props.date}</p>
                           </div>
                           <div class="note_column">
                               <h3>–î—Ä—É–≥–∏–µ –∑–∞–ø–∏—Å–∏:</h3>
                               {this.props.country === "argentina" ? <Argentina /> : null }
                               {this.props.country === "mexico" ? <Mexico /> : null }
                               {this.props.country === "ecuador" ? <div> <Ecuador /> <Costarica />  <Norway /> <Europe /> <Mexico />  </div> : null }
                               {this.props.country === "peru" ? <div> <Peru /> <Argentina /> </div>: null }
                               {this.props.country === "colombia" ? <div> <Colombia /> <Costarica />  <Norway /> <Europe /> <Mexico />  </div>  : null }
                               {this.props.country === "costarica" ? <div> <Costarica />  <Norway /> <Europe /> <Colombia /> <Mexico /> </div> : null }
                               {this.props.country === "chile" ? <div> <Chile /> <Mexico /> </div> : null }
                               {this.props.country === "norway" ? <div>    <Norway /> <Europe /> <Colombia /> <Costarica /> <Peru /> <Mexico />  </div> : null }
                               {this.props.country === "europe" ? <div> <Europe />  <Colombia /> <Costarica /> <Peru /> <Mexico /> </div> : null }
                               {this.props.country === "brasil" ? <div> <Brasil /> <Europe />  <Colombia /> <Costarica /> <Peru /> <Mexico /> </div> : null }
                               {this.props.country === "italy" ? <div> <Italy /> <Brasil /> <Europe />  <Colombia /> <Costarica /> <Peru /> <Mexico /> </div> : null }             
                          </div>
                       </div>
                   </div>
                   <Footer />
           </div>
               )

    }
  
}

export default BlogContent;