import React from 'react'

import "../sass/main.sass"

import {Link} from 'react-router-dom';

import machu from "../img/machu.jpg"
import mar from "../img/mar.jpg"
import alpaca from "../img/alpaca.jpg"
import mate from "../img/mate.jpg"
import jacaranda from "../img/jacaranda.jpg"
import rio from "../img/rio.jpg"
import lecce from "../img/lecce.jpg"
import palms from "../img/palms.png"
import spain from "../img/spain.png"
import avocado from "../img/avocado.png"
import Map from "./Map"
import map from "../img/map.png"
import plane from "../img/plane_button.png"

import {Animated} from "react-animated-css";


class Main extends React.Component {

  constructor(props) {
    super(props);
    this.scrollDiv = React.createRef();
    
  }

  state = {
    setIsShown: false
  }

  changePic = () => {
    let plane = document.getElementById("plane")
  
  }
  

render(){
  return (
    <div>
   
  <div className="container">

<Animated animationIn="slideInLeft" animationOut="fadeOut" animationInDelay={500}  animationInDuration={500} isVisible={true}>
  <img class="machu" src={machu} alt="" />
  </Animated>
  <Animated animationIn="fadeIn" animationOut="fadeOut" animationInDelay={1000} animationInDuration={1000} isVisible={true}>
  <img class="mar" src={mar} alt="" />
  </Animated>
  
  {this.state.setIsShown == true ? <Animated animationIn="slideInLeft" animationOut="slideOutRight" animationInDuration={1000} animationOutDuration={1000} isVisible={true}>
 <img id="plane" className="plane" src={plane} /></Animated> : null}
  

 <Animated animationIn="fadeIn" animationOut="fadeOut" animationInDelay={1000} animationInDuration={500} isVisible={true}>
 <img class="alpaca" src={alpaca} /></Animated>

 <Animated animationIn="fadeIn" animationOut="fadeOut" animationInDelay={1000} animationInDuration={500} isVisible={true}>
 <img class="mate" src={mate} /></Animated>

 <Animated animationIn="fadeIn" animationOut="fadeOut" animationInDelay={1500} animationInDuration={500} isVisible={true}>
 <img class="jacaranda" src={jacaranda} /></Animated>
 <Animated animationIn="fadeIn" animationOut="fadeOut" animationInDelay={1500} animationInDuration={500} isVisible={true}>
 <img className="spain" src={spain} />
 </Animated>
 <Animated animationIn="bounceInUp" animationOut="fadeOut" animationInDelay={1700} animationInDuration={500} isVisible={true}>
 <img class="rio" src={rio} />
 </Animated>

 <Animated animationIn="fadeIn" animationOut="fadeOut" animationInDelay={1900} animationInDuration={800} isVisible={true}>
 <img class="italia" src={lecce} /> </ Animated>
 <Animated animationIn="fadeIn" animationInDelay={1500} animationOut="fadeOut" animationInDuration={100} isVisible={true}>
 <img className="avocado" src={avocado} /> </Animated>
 
 

   <div className="main_text">
     <h1>–ö—Å–µ–Ω–∏—è - –ø—É—Ç–µ—à–µ—Å—Ç–≤–µ–Ω–Ω–∏—Ü–∞</h1>
   </div>
   <div className="button_country" 
   onClick={() => {
       this.scrollDiv.current.scrollIntoView({behavior: "smooth"})
     }}
  onMouseEnter={()=> this.setState ( {setIsShown: true} )}
  onMouseLeave={() => this.setState ( {setIsShown: false} )}
     
     > <h2>–í—ã–±—Ä–∞—Ç—å —Å—Ç—Ä–∞–Ω—É</h2> </div>

 </div>

 <div className="map_main" ref={this.scrollDiv}>
 <Map />

 </div>

 </div>
  )
 
}
}


export default Main