import React from 'react'

import ArticleListItem from "../blog/ArticleListItem"
import BlogMenu from './BlogMenu';
import MapArgentina from "../blog/MapArgentina"
import client from "../blog/utils/client"


class Argentina extends React.Component {  
    state = {
    articles: [],
    isLoading: true
  }  
  
  componentDidMount() {
    client.getEntries({
      "content_type":"blogPost",
        "fields.country": "argentina"
    }
    ).then((entries) => {
      entries.items.forEach(entry => {
        this.setState({
          articles: entries.items,
          isLoading: false})
      })
    }
    )}

  

  render() {
  const articles = this.state.articles.map((article, i) =>
      <ArticleListItem key={i} article={article} />
      )
   
    return (
      <div>
        {this.state.isLoading ? <div>
        <p className="loading">–ó–∞–≥—Ä—É–∑–∫–∞...</p> 
        </div> : <div className="article_list_argentina">{articles}</div> }
   
      </div>
    
    )
  }
}

export default Argentina