import React from 'react'
import marked from 'marked';
import BlogContent from "./BlogContent"
import client from "./utils/client"

class BlogItem extends React.Component {
    constructor() {
        super()
        this.state = {
            content: null,
            slug: null,
            title: null,
            date: null,
            country: null
        }
    }

  
    componentDidMount() {
        if (!this.props.match) {
          return;
        } else {
          this.callApi()
        }
       
      }
    
      componentDidUpdate() {
        this.callApi();
      }
    
      callApi = () => {
        client
          .getEntries({
            content_type: "blogPost",
            "fields.slug": this.props.match.params.slug
          })
          .then(response => {
            console.log(response);
            this.setState(
              {
                content: response.items[0].fields.content,
                title: response.items[0].fields.title,
                date: response.items[0].fields.date,
                country: response.items[0].fields.country
              },
              () => console.log(this.state)
            );
          });
      };

getParsedMarkdown(content) {
    return {
        __html: marked(content || "", {sanitize: true})
    }
}

render() {
  let content = this.state.content
    return (   
       <BlogContent 
        title={this.state.title}
        content={this.getParsedMarkdown(content)}
        date={this.state.date}
        country={this.state.country}
        slug={this.state.slug}
         />
    )
}

}
 


export default BlogItem